import { Component, OnInit, Renderer2, Inject } from '@angular/core';
import { DOCUMENT } from '@angular/common';

@Component({
  selector: 'app-gltf',
  templateUrl: './gltf.component.html',
  styleUrls: ['./gltf.component.css']
})
export class GltfComponent implements OnInit {

  constructor(private renderer2: Renderer2, @Inject(DOCUMENT) private _document) { 
    this.loadScripts();
  }

  // Method to dynamically load JavaScript 
  loadScripts() { 
  
    // This array contains all the files/CDNs 
    const dynamicScripts = [
      'assets/js/webgl.js',
      'assets/js/jeelizFaceFilterES6.js',
      'assets/js/stl/three.js',
      // 'https://threejs.org/build/three.js',
      'assets/js/stl/STLLoader.js',
      'assets/js/JeelizResizer.js',
      'assets/js/JeelizThreejsHelper.js',
      'assets/js/demo.js',
    ]; 
    for (let i = 0; i < dynamicScripts.length; i++) { 
      const node = document.createElement('script'); 
      node.src = dynamicScripts[i]; 
      node.type = 'text/javascript'; 
      node.async = false; 
      document.getElementsByTagName('head')[0].appendChild(node); 
    } 
 }

  ngOnInit(): void {
  }

}
