import { Component, OnInit, AfterViewInit, ElementRef, ViewChild, Input } from '@angular/core';

import videojs from 'video.js';
import 'videojs-vr/dist/videojs-vr.js';

@Component({
  selector: 'app-videojs-viewer',
  templateUrl: './videojs-viewer.component.html',
  styleUrls: ['./videojs-viewer.component.css']
})
export class VideojsViewerComponent implements OnInit, AfterViewInit {

  @Input() public url: string;
  private readonly config: any;
  private player: any;
  @ViewChild('my_video')
  private element: ElementRef;

  constructor() {
    // video.js configuration
    this.config = {
      controls: false,
      autoplay: true,
      fluid: false,
      loop: false,
      width: 640,
      height: 440
    };
  }

  ngOnInit() {
    console.log('URL:', this.url)
  }

  // using ngAfterViewInit so that videojs element
  // after the component template itself has been rendered
  ngAfterViewInit() {

    // setup the player
    this.player = videojs(this.element.nativeElement, this.config, () => {
      console.log('Using video.js ' + videojs.VERSION);
      this.player.src(this.url);
      this.player.vr({projection: 'EAC'});
    });

    // error handling
    this.player.on('error', (error) => {
      console.warn(error);
    });
  }

}
